}
.header{
    position: fixed;
    height: 70px;
    background-image: url("https://images.alphacoders.com/201/201831.png");
    width: 100%;
    border-radius: 7px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    z-index: 1;
    padding-bottom: 35px;
    padding-top: 5px;
}
h1{
    font-family: 'Pokemon Solid', sans-serif;   
